import React from 'react';
import chroma from 'chroma-js';
const SidebarSwatches = (props) => {
    const getColors = () => {
        const values = String(props.value).split(' ');
        const colors = [];
        values.map((value) => {
            if (value.endsWith('),')) {
                let string = value.slice(0, value.length - 1);
                if (chroma.valid(string)) {
                    colors.push(string);
                }
            }
            else if (value.endsWith(')')) {
                if (chroma.valid(value)) {
                    colors.push(value);
                }
            }
        });
        return colors;
    };
    const colors = getColors();
    return (React.createElement("div", { className: 'c-sidebar-styles__swatch-group' },
        colors.length > 0
            ? colors.map((color, index) => (React.createElement("div", { key: index, className: 'c-sidebar-styles__swatch', style: { backgroundColor: color } })))
            : null,
        String(props.value).startsWith('url')
            ? React.createElement("div", { className: 'c-sidebar-styles__swatch c-sidebar-styles__swatch--image', style: {
                    background: props.value,
                    backgroundRepeat: 'no-repeat',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center center'
                } })
            : null));
};
export default SidebarSwatches;
