import React from 'react';
const SidebarLeftArtboard = (props) => {
    const { artboard, selection, setSelection, setGroupSelection, setGroupSelectionNest, setHover } = props;
    const handleDoubleClick = () => {
        setGroupSelection(null);
        setGroupSelectionNest(null);
    };
    return (React.createElement("div", { className: `c-sidebar-left__layer c-sidebar-left__layer--header ${selection && artboard.id === selection.id
            ? 'c-sidebar-left__layer--active'
            : null}`, onClick: () => setSelection(artboard), onDoubleClick: handleDoubleClick, onMouseOver: () => setHover(artboard), onMouseOut: () => setHover(null) },
        React.createElement("span", { className: 'c-sidebar-left-layer__name' }, artboard.name)));
};
export default SidebarLeftArtboard;
